.page-title {
	text-align: center;
	text-transform: capitalize;

	.home .page-header & {
		@include bp(custom, 0, 1200px) {
		    font-size: 28px;
		    margin-bottom: 25px;
		}
		@include bp(phone) {
			font-size: 16px;
		}
	}
}

.page-header {
	.button,
	.home & .button {
		padding: 10px 24px;
	}
}

.header-outer {
	// background: url(assets/img/temp-header.jpg) center no-repeat;
	// background-size: cover;
	background: linear-gradient(90deg, rgba(57, 160, 165, 1) 0%, rgba(55, 154, 168, 1) 27.19%, rgba(49, 139, 175, 1) 61.98%, rgba(39, 113, 187, 1) 100%);
	opacity: 0.8;
	overflow: hidden;
	@include bp(phone) {
		overflow: visible;
	}
}

.nav-menu {
	.nav-menu-search,
	.current-cart {
		display: none;
	}
}

.header-background {
	background: url('/wp-content/uploads/2016/01/icon_antivirus.png') no-repeat center / cover;
	width: 30%;
	height: 100%;
	position: absolute;
	margin-left: 15%;
	opacity: 0.3;
	z-index: -1;
	top: 50%;
	transform: translateY(-50%);
	@include bp(phone) {
		width: 60%;
		height: 100%;
		margin-left: 0;
	}
}

.site-title {
	@include bp(phone) {
		font-size: 20px;
		max-width: 80%;
	}
}
