.kaseya-category-grid {
    background: $gray-lt;
}

.category-section-header {
    h2 {
        padding: 25px 0;
    }
}

.category-grid-item {
	@include bp(phone) {
		width: 50%;
	}

    .icon {
		max-width: 80%;
		padding-bottom: 100%;
		background: center center no-repeat;
		background-size: contain;
		margin: 0 auto;
    }

    h3 {
        min-height: 65px;
        margin: 20px auto;
        line-height: 1.2em;
		@include bp(custom, 0, 1024px) {
			font-size: 1.2em;
		}
    }

	a {
		color: $blue-md;
		text-decoration: none;
	}
}
