.kaseya-downloads-shortcode {
    text-align: center;
    padding: 20px;
    margin: 0 auto;
    .more-link {
        color: $orange;
        font-size: 0.5em;
        line-height: 1em;
        text-decoration: underline;
		@include bp(phone) {
			display: block;
		}
    }
    .grid {
        margin-bottom: 50px;
    }
}
.download-section-header {
    h2 {
        padding: 25px 0;
    }
}
.download {
    border: 1px solid gray;
    display: inline-block;
    vertical-align: top;
    background: $blue-md;
	margin-bottom: 25px;
    &:nth-of-type(2) {
        background: $blue-lt;
    }
    &:nth-of-type(3) {
        background: $blue-dk;
    }
    .info {
        padding: 15px;
        color: white;
        text-align: center;
        h3 {
            font-size: 23px;
			color: white;
        }
        .button {
            background: rgba($green, 1);
            border: 2px solid white;
            border-radius: 10px;
            padding: 10px 30px;
            color: white;
            margin: 20px 0;
            transition: 0.3s;
            &:hover {
                background: rgba($green, 0.6);
            }
        }
        .ratings {
            .edd_reviews_rating_box {
                float: none;
                margin: 0 auto;
            }
        }
    }
    .description {
        padding: 15px;
        position: relative;
        text-align: left;
        background: white;
        min-height: 150px;
        p {
            max-width: 75%;
            font-size: 16px;
        }
        .icon {
            width: 75px;
            height: 75px;
            position: absolute;
            right: 15px;
            top: -35px;
            overflow: hidden;
        }
    }
}
