.edd_reviews_rating_box,
.edd_star_rating {
	background-image: url('assets/img/stars.png');
	&[data-rating="0"] {
		background-position: bottom left;
	}
}
.search-wrapper {
	text-align: center;
	background: $gray-lt;
	padding: 40px;
	vertical-align: top;
	.form-title {
		display: inline-block;
		font-size: 32px;
		text-transform: uppercase;
		vertical-align: top;
		line-height: 41px;
	}
	.search-form {
		width: 500px;
		max-width: 100%;
		display: inline-block;
		vertical-align: top;
		input {
			border-right: 0;
			float: left;
		}
		.search-submit {
			background: $orange;
			border: 2px solid $gray-md;
			border-left: none;
			width: 41px;
			float: left;
		}
	}
	.search-field {
		width: 86%;
	}
}

.et_social_inline {
	.et_social_networks {
		@include bp(custom, 0, 768px) {
			text-align: center;
		}
		li {
			float: none;
			display: inline-block;
			width: 40px !important;
		}
		div.et_social_network_label {
			display: none;
		}
	}
	.et_social_4col {
		li {
			width: 40px;
			margin-left: 0;
			margin-right: 10px;
		}
	}
	.et_social_flip.et_social_left {
		li:hover {
			.et_social_icon:before {
				width: 40px;
			}
		}
	}
}

.screen-reader-text {
    clip: rect(1px, 1px, 1px, 1px);
    position: absolute !important;
    height: 1px;
    width: 1px;
    overflow: hidden;
}

body.search .site-content.container {
	max-width: 1170px;
}

.js-toggle-search {
	display: none;
}
