.single-download {
	.download-header__info,
	.download-gallery {
		display: none;
	}
	.site-content {
		margin-top: 4em;
		margin-bottom: 4em;
		@include bp(phone) {
			margin-top: 0;
			margin-bottom: 0;
		}
	}
	.content-area {
		@include bp(phone) {
			margin-top: 0;
			margin-bottom: 0;
			padding: 15px;
		}
	}
	.col-1-4 {
		padding-right: 0;
		@include bp(custom, 0, 768px) {
			margin-top: 25px;
		}
	}
	.entry-content .edd_download_purchase_form {
		border: 0;
		margin: 0;
		padding: 0;
	}
	.edd_price {
		padding: .25em .75em;
		display: block;
		text-align: center;
		font-size: 3em;
		font-weight: normal;
	}
	.download-meta {
		background: $gray-lt;
		padding: .5em;
		.edd_reviews_rating_box {
			float: none;
			margin: 20px auto;
		}
	}
	.edd_purchase_submit_wrapper a.edd-add-to-cart,
	.edd-submit.button.blue {
		background: $orange;
		border-color: $orange;
	}
	.edd_reviews_rating_box,
	.edd_star_rating,
	.comment_form_rating .edd_reviews_rating_box a:hover ~ a,
	.comment_form_rating .edd_reviews_rating_box a:hover{
		background-image: url('assets/img/stars-blue.png');
	}
	.col-3-4,
	.col-1-4 {
		@include bp(custom, 0, 768px) {
			width: 100%;
			padding: 0;
		}
	}
}

.page-header--download {
	padding-bottom: 6em;
}

.tabs {
	min-width: 320px;
	margin: 0 auto;
	background: #fff;


	section {
	  display: none;
	  padding: 20px;
	  border: 1px solid #ddd;
	  min-height: 300px;
	}

	> input {
	  display: none;
	}

	> label {
		display: inline-block;
		margin: 0 0 -1px;
		padding: 15px 25px;
		font-weight: 600;
		text-align: center;
		color: #bbb;
		border: 1px solid transparent;
		@include bp(phone) {
			font-size: 12px;
			padding: 10px;
		}
	}

	> label:before {
	  font-family: fontawesome;
	  font-weight: normal;
	  margin-right: 10px;
	}

	> label:hover {
	  color: #888;
	  cursor: pointer;
	}

	> input:checked + label {
	  color: #555;
	  border: 1px solid #ddd;
	  border-top: 2px solid orange;
	  border-bottom: 1px solid #fff;
	}

	#tab1:checked ~ #content1,
	#tab2:checked ~ #content2,
	#tab3:checked ~ #content3,
	#tab4:checked ~ #content4 {
	  display: block;
	}

	.comments-area {
		margin: 0;
		.comment-reply-title {
			display: none;
		}
	}
	table {
		.title {
			width: 200px;
			color: $gray-dk;
			text-transform: uppercase;
		}
	}
}
